import gql from 'graphql-tag'

const LIST_RETURNS = gql`query listReturns($startDate: DateTime, $endDate: DateTime, $numberGuide: String, $cursor: String){
    returns(guide_Icontains: $numberGuide, after: $cursor, returnedDate_Date_Gte: $startDate, returnedDate_Date_Lte: $endDate){
        pageInfo{
            hasNextPage
        }
        edges{
            cursor
            node{
                guide
                store{
                    name
                }
                productpackagethroughSet{
                    quantity
                    product{
                        name
                    }
                }
                returnedDate
            }
        }
    }
}`

export {
    LIST_RETURNS
}