<template>
  <layout>
    <page-header :title="title"></page-header>
    <template>
      <div>
        <b-card class="mb-2">
          <b-card-text>
            <b-row>
              <b-col md="4">
                <b-form-group class="mb-2">
                  <b-form-datepicker
                    v-model="startDate"
                    placeholder="Fecha Inicial"
                    :max="maxDate"
                  >
                  </b-form-datepicker>
                </b-form-group>
              </b-col>
              <b-col md="4">
                <b-form-group class="mb-2">
                  <b-form-datepicker
                    v-model="endDate"
                    placeholder="Fecha Final"
                    :max="maxDate"
                    :min="minEndDate"
                  >
                  </b-form-datepicker>
                </b-form-group>
              </b-col>
              <b-col md="1">
                <b-button
                  :disabled="startDate !== null && endDate !== null ? false : true"
                  @click="getReturns(true)"
                  variant="success"
                >
                  Enviar
                </b-button>
              </b-col>
              <b-col md="1">
                <b-button
                  v-show="startDate && endDate"
                  @click="clearDates()"
                  variant="info"
                >
                  Limpiar
                </b-button>
              </b-col>
            </b-row>
            <b-col lg="4" md="4">
              <b-form @submit.prevent class="mb-3">
                <b-form-group>
                  <b-input-group>
                    <b-form-input
                      id="input-1"
                      v-model="search"
                      type="text"
                      placeholder="Buscar por número de guía"
                      required
                    ></b-form-input>
                    <b-input-group-append>
                      <b-button
                        type="submit"
                        :disabled="!search"
                        @click="getReturns(true)"
                        variant="warning"
                        >Buscar</b-button
                      >
                    </b-input-group-append>
                  </b-input-group>
                </b-form-group>
              </b-form>
            </b-col>
            <template v-if="loadingReturns">
              <b-skeleton-table
                :rows="10"
                :columns="4"
                :table-props="{ bordered: true, striped: true }"
              ></b-skeleton-table>
            </template>
            <template v-else>
              <div class="table-responsive">
                <b-table
                  striped
                  hover
                  :items="returnsTable"
                  :fields="fields"
                  responsive="sm"
                >
                  <template v-slot:cell(actions)="{ item }">
                    <b-button
                      @click="searchDispatch(item.guide)"
                      v-b-modal.modal-lg
                      variant="primary"
                    >
                      <i class="mdi mdi-eye"></i>
                    </b-button>
                  </template>
                </b-table>
              </div>
            </template>
          </b-card-text>
        </b-card>
        <div class="text-center mt-2" v-if="hasNextPage">
          <b-button
            block
            variant="primary"
            @click="getReturns()"
            :disabled="loadingMore"
          >
            <template v-if="loadingMore">
              <b-spinner small type="grow"></b-spinner>
              Cargando...
            </template>
            <template v-else> Ver más </template>
          </b-button>
        </div>
      </div>
      <b-modal
        v-if="returnSelected"
        id="modal-lg"
        size="lg"
        :title="`Devolución ${returnSelected.guide}`"
        :no-close-on-backdrop="true"
        :no-close-on-esc="true"
      >
        <b-row>
          <div class="col-md-6" v-for="(item, index) in returnSelected.products" :key="index">
            <b-card>
              <b-card-text>
                <strong>{{ item.product.name }}:</strong> {{ item.quantity }}
              </b-card-text>
            </b-card>
          </div>
        </b-row>
      </b-modal>
    </template>
  </layout>
</template>
<script>
import Layout from "@/router/layouts/main";
import PageHeader from "@/components/page-header";
import { LIST_RETURNS } from "@/api/graphql/query/returns";
import { toast } from "@/utils/alerts";
import { formatDate } from '@/utils/general'

export default {
  name: "Returns",
  components: {
    Layout,
    PageHeader,
  },
  data() {
    const now = new Date()
    const today = new Date(now.getFullYear(), now.getMonth(), now.getDate())
    
    return {
      title: "Devoluciones",
      loadingReturns: true,
      loadingMore: false,
      hasNextPage: null,
      lastCursor: null,
      returnsTable: [],
      guideNumber: null,
      search: null,
      startDate: null,
      endDate: null,
      maxDate: today,
      minEndDate: null,
      fields: [
        {
          key: "guide",
          label: "Guía",
        },
        {
          key: "store",
          label: "Tienda"
        },
        {
          key: "products",
          label: "Productos"
        },
        {
          key: "returned_date",
          label: "Fecha devolución",
        },
        {
          key: "actions",
          label: "Acciones",
        },
      ],
      returnDetail: [],
      returnSelected: null,
    };
  },
  watch: {
    search: function (newValue) {
      if (newValue === "") {
        this.returnsTable = [];
        this.lastCursor = null;
        this.getReturns();
        this.loadingReturns = true;
      }
    },
    startDate: function(newValue){
      if (this.startDate > this.endDate){
        this.endDate = this.startDate
      }
      const startDate = new Date(this.startDate)
      const minDate = new Date(startDate.getFullYear(), startDate.getMonth(), startDate.getDate() + 1)
      this.minEndDate = minDate
    }
  },
  methods: {
    async getReturns(isSearch = false) {
      if (isSearch === true) {
        this.loadingReturns = true;
        this.returnsTable = [];
        this.lastCursor = null;
      }
      this.loadingMore = true;
      try {
        const response = await this.$apollo.query({
          query: LIST_RETURNS,
          variables: {
            cursor: this.lastCursor,
            numberGuide: this.search,
            startDate: this.startDate !== null ? new Date(this.startDate): null,
            endDate: this.endDate !== null ? new Date(this.endDate): null
          },
        });
        const { returns } = response.data;
        if (returns !== null) {
          const data = returns.edges;
          this.hasNextPage = returns.pageInfo.hasNextPage;
          this.formatDataTable(data);
        } else {
          toast("error", "No se pudo recuperar la información");
        }
        this.loadingReturns = false;
        this.loadingMore = false;
      } catch (error) {
        this.loadingReturns = false;
        this.loadingMore = false;
        toast("error", "No se pudo recuperar la información");
      }
    },
    formatDataTable(data) {
      for (let item of data) {
        const tempDataTable = {
          guide: item.node.guide,
          returned_date: formatDate(item.node.returnedDate),
          products: this.sumQuantityProducts(
            item.node.productpackagethroughSet
          ),
          store: item.node.store.name
        };
        const tempDataDetail = {
          guide: item.node.guide,
          products: item.node.productpackagethroughSet,
        };
        this.returnsTable.push(tempDataTable);
        this.returnDetail.push(tempDataDetail);
        this.lastCursor = item.cursor;
      }
    },
    sumQuantityProducts(products) {
      let count = 0;
      for (let item of products) {
        count += item.quantity;
      }
      return count;
    },
    searchDispatch(guide) {
      const index = this.returnDetail.findIndex(
        (dispatch) => dispatch.guide === guide
      );
      if (index !== -1) {
        this.returnSelected = this.returnDetail[index];
      }
    },
    clearDates(){
      this.startDate = null
      this.endDate = null
      this.returnsTable = []
      this.lastCursor = null
      this.hasNextPage = null
      this.loadingReturns = true
      this.getReturns()
    }
  },
  beforeMount() {
    this.getReturns();
  },
};
</script>